<template>
  <div :style="backgroundImage"
       class="lalala">
    <el-container>
      <el-header class="homeHeader"
                 style="background: #394b59;">
        <div class="lfp">Data Monitoring </div>
        <el-dropdown @command="commandHandler">
          <span class="el-dropdown-link">
            <span style="display:inline-block;margin-top: 30px;">{{user.username}}</span><i><img style="float: right;width: 50px;"
                   src="../assets/r3.png"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="logout">注销登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-header>
      <el-container>
        <el-aside width="200px"
                  class="yangshi">
          <el-menu router
                   v-bind:style="containerStyleObject"
                   background-color="#1b2632"
                   unique-opened>
            <el-submenu :index="index + ''"
                        v-for="(item,index) in routes"
                        :key="index"
                        v-if="item.hidden != 1">
              <template slot="title">
                <i :class="item.iconCls"
                   style="color:#fff; margin-right: 10px;"></i>
                <span style="color:#fff;">{{item.name}}</span>
              </template>
              <el-menu-item :index="children.path"
                            v-for="(children,indexj) in item.children"
                            v-if="children.hidden != 1"
                            style="color:#fff;">
                {{children.name}}
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main style="shouye">
          <div>
            <el-breadcrumb separator-class="el-icon-arrow-right"
                           v-if="this.$router.currentRoute.path != '/home'">
              <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
              <el-breadcrumb-item>{{this.$router.currentRoute.name}}</el-breadcrumb-item>
            </el-breadcrumb>
            <div class="zm"
                 v-if="this.$router.currentRoute.path == '/home'">

            </div>
            <router-view />
          </div>

        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      userimg: '@/assets/80d45225001d7d27c7d729593970efe.jpg',
      backgroundImage: {
        backgroundImage:
          'url(' +
          require('@/assets/0189415b51b518a8012036be65a449.jpg@2o.jpg') +
          ')',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
        backgroundSize: '100% 100%',
      },
      containerStyleObject: {
        height: '',
      },
      user: JSON.parse(window.sessionStorage.getItem('user')),
    }
  },
  computed: {
    routes() {
      return this.$store.state.routes
    },
  },
  // mounted() {
  //   console.log(window.sessionStorage.getItem('user'))
  // },
  mounted() {
    this.autodivheight()
  },
  methods: {
    commandHandler(command) {
      if (command == 'logout') {
        this.$confirm('此操作将注销登录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        })
          .then(() => {
            // 注销登录
            this.postRequest('/home/Login/logout')
            // 清空用户信息
            window.sessionStorage.removeItem('tokenStr')
            window.sessionStorage.removeItem('user')
            //清空菜单信息
            this.$store.commit('initRoutes', [])
            // 跳转到登录页面
            this.$router.replace('/')
          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消操作',
            })
          })
      }
    },
    autodivheight() {
      //函数：获取尺寸
      //获取浏览器窗口高度
      var winHeight = 0
      if (window.innerHeight) winHeight = window.innerHeight
      else if (document.body && document.body.clientHeight)
        winHeight = document.body.clientHeight
      //通过深入Document内部对body进行检测，获取浏览器窗口高度
      if (document.documentElement && document.documentElement.clientHeight)
        winHeight = document.documentElement.clientHeight
      //DIV高度为浏览器窗口的高度
      this.containerStyleObject.height = winHeight + 'px'
    },
  },
}
</script>

<style>
.el-breadcrumb {
  margin-bottom: 12px;
}
.homeHeader {
  opacity: 0.8;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  box-sizing: border-box;
}

.homeHeader .lfp {
  font-size: 30px;
  color: white;
}

.el-dropdown-link {
  color: white;
}

.el-dropdown-link img {
  margin-left: 12px;
}

.zm {
  display: block;
  font-size: 30px;
  color: #409eff;
  padding-top: 50px;
  text-align: center;
  font-family: 华文楷体;
}

.yangshi {
  color: white;
  opacity: 0.8;
}

.lalala {
  width: 100%;
  height: 100%;
}
.el-aside .el-menu {
  border-right: none;
}
</style>