<template>
  <div class="dslfp">
    <div>
      <el-button type="primary"
                 size="small"
                 @click="zabbixUserUp"
                 round>zabbix用户更新</el-button>
    </div>
    <div class="zabbixInfo">
      <el-input placeholder="请输入json版本"
                class="zabbixInfoInput"
                size="small"
                @keydown.enter.native="updateApiInfo"
                v-model="zabbixApiInfo.jsonrpc">
        <template slot="prepend">json版本</template>
      </el-input>
      <el-input placeholder="请输入ip"
                class="zabbixInfoInput"
                size="small"
                @keydown.enter.native="updateApiInfo"
                v-model="zabbixApiInfo.ip">
        <template slot="prepend">ip</template>
      </el-input>
      <el-input placeholder="请输入api接口"
                class="zabbixInfoInput"
                size="small"
                @keydown.enter.native="updateApiInfo"
                v-model="zabbixApiInfo.user_login">
        <template slot="prepend">api接口</template>
      </el-input>
      <el-input placeholder="请输入请求接口"
                class="zabbixInfoInput"
                size="small"
                @keydown.enter.native="updateApiInfo"
                v-model="zabbixApiInfo.user_get">
        <template slot="prepend">请求接口</template>
      </el-input>
      <el-button size="small"
                 type="primary"
                 icon="el-icon-plus"
                 @click="updateApiInfo">修改</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'sys_zabbix_info',
  data() {
    return {
      zabbixApiInfo: {
        jsonrpc: '',
        ip: '',
        user_login: '',
        user_get: '',
      },
    }
  },
  mounted() {
    this.initZabbixInfo()
  },
  methods: {
    updateApiInfo() {
      if (
        this.zabbixApiInfo.jsonrpc &&
        this.zabbixApiInfo.ip &&
        this.zabbixApiInfo.user_login &&
        this.zabbixApiInfo.user_get
      ) {
        this.postRequest('/home/Sys/sys_user_info_up', this.zabbixApiInfo).then(
          (resp) => {
            console.log(resp)
            if (resp.code == 200) {
              this.$message.success(resp.success)
              this.initZabbixInfo()
            } else {
              this.$message.error(resp.error)
              return
            }
          }
        )
      } else {
        this.$message.error('接口参数不能为空')
      }
    },
    // 初始化
    initZabbixInfo() {
      this.getRequest('/home/Sys/sys_zabbix_info').then((resp) => {
        if (resp.code == 200) {
          this.zabbixApiInfo = resp.data
        } else {
          this.$message.error(resp.error)
          return
        }
      })
    },
    // 点击事件
    zabbixUserUp() {
      this.getRequest('/home/Sys/sys_zabbix_user_up').then((resp) => {
        if (resp.code == 200) {
          this.$message.success(resp.info)
        } else {
          this.$message.error(resp.error)
          return
        }
      })
    },
  },
}
</script>

<style>
.dslfp {
  background-color: #fff;
}
.zabbixInfo {
  margin-top: 20px;
}
.zabbixInfoInput {
  width: 300px;
  margin-right: 8px;
  margin-bottom: 8px;
}
</style>
